import navigationBar from '@/components/navigation-bar.vue' //引入自定义导航栏
export default {
	components: {
		navigationBar
	},
	data() {
		return {
			//设置导航栏样式
			navigationBarStyle: {
				iconText: 'VIP会员规则' //导航栏文字
			},
			isUpgrade: true //是否显示升级规则
		}
	},
	onLoad(options) {
		this.isUpgrade = options.isUpgrade
		this.navigationBarStyle.iconText = this.isUpgrade =='true' ? 'VIP会员升级规则' : 'VIP会员规则'
	},
	methods: {
		
	}
}
